/* globals setTimeout */
import React from 'react';
import Header from './Header.jsx';
import Board from './Board.jsx';
import Keyboard from './Keyboard.jsx';
import KeyHandler from './KeyHandler.jsx';
import Modal from './Modal.jsx';
import Toaster from './Toaster.jsx';
import Stats from './Stats.jsx';
import {
  letterUsage, validLetter,
  readStats, writeStats,
  readState, writeState,
  getSeed
} from './utils.js';
import { getTargetWord, inDictionary } from './dictionary.js';
import css from './Game.css';

export default class Game extends React.PureComponent {
  constructor () {
    super();
    const oldState = readState();
    this.state = {
      reject: null,
      modal: 'stats',
      stats: readStats(),
      currentAttempt: '',
      ...(oldState || this.getNewGameData())
    };
    console.info(this.state.keyword);
  }

  getNewGameData () {
    const seed = getSeed();
    return {
      gameStatus: '',
      keyword: getTargetWord(seed),
      rowIndex: 0,
      id: seed,
      attempts: [ '', '', '', '', '', '' ]
    };
  }

  onModalClose = () => {
    this.setState({ modal: null });
  };

  onGameOver () {
    const { gameStatus, rowIndex } = this.state;
    const stats = readStats();
    const isWin = gameStatus === 'solved';
    stats.gamesPlayed++;
    if (isWin) {
      stats.currentStreak++;
      if (stats.maxStreak < stats.currentStreak) {
        stats.maxStreak = stats.currentStreak;
      }
      stats.gamesWon++;
    }
    else {
      stats.currentStreak = 0;
    }
    // guesses stats
    if (isWin) {
      stats.guesses[rowIndex]++;
    }
    else {
      stats.guesses.fail++;
    }
    writeStats(stats);
    this.setState({ stats });
  }

  onCommitAttempt = () => {
    // We're committing, check if game over
    const { keyword, attempts, rowIndex } = this.state;
    // is the word solved?
    if (attempts.some(d => d === keyword)) {
      this.setState({ gameStatus: 'solved' }, () => {
        writeState(this.state);
        // make something good happen!
        // FIXME: show the modal only once the word has been shown!
        setTimeout(() => {
          this.onGameOver();
          this.setState({ modal: 'stats' });
        }, 2000);
      });
    }
    // is the game lost?
    else if (rowIndex >= 6) {
      this.toaster.toast(this.state.keyword, Infinity);
      this.setState({ gameStatus: 'lost' }, () => {
        writeState(this.state);
        // make something bad happen!
        setTimeout(() => {
          this.onGameOver();
          this.setState({ modal: 'stats' });
        }, 2000);
      });
    }
  };

  onKey = e => {
    const { key, meta } = e;
    const { attempts, rowIndex, gameStatus, currentAttempt } = this.state;
    if (!meta && gameStatus === '') {
      if (key.toLowerCase() === 'backspace') {
        this.setState({
          currentAttempt: currentAttempt.slice(0, currentAttempt.length - 1)
        });
      }
      else if (key.toLowerCase() === 'escape') {
        this.setState({ currentAttempt: '' });
      }
      else if (validLetter(key)) {
        this.setState({
          currentAttempt: (currentAttempt + key.toUpperCase()).slice(0, 5)
        });
      }
      else if (key.toLowerCase() === 'enter') {
        // commit attempt
        let reject = '';
        if (currentAttempt.length !== 5) {
          reject = 'Það vantar fleiri stafi.';
        }
        else if (!inDictionary(currentAttempt)) {
          reject = 'Orðið er ógilt.';
        }
        if (reject) {
          this.toaster.toast(reject, 1);
          // FIXME: this is a bit of a mess:
          this.setState({ reject }, () => {
            setTimeout(() => {
              this.setState({ reject: '' });
            }, 1000);
          });
        }
        else {
          this.setState({
            rowIndex: rowIndex,
            currentAttempt: '',
            attempts: attempts.map((d, i) => (i === rowIndex ? currentAttempt : d))
          }, this.onCommitAttempt);
        }
      }
    }
  };

  render () {
    const { reject, attempts, rowIndex, keyword, currentAttempt, modal, stats, gameStatus } = this.state;
    return (
      <div className={css.game}>
        <Header
          onInfo={() => (this.setState({ modal: 'info' }))}
          onStats={() => (this.setState({ modal: 'stats' }))}
          onSettings={() => (this.setState({ modal: 'settings' }))}
          />
        <KeyHandler onKey={this.onKey} />
        <Board
          keyword={keyword}
          attempts={attempts.map((d, i) => (i === rowIndex ? currentAttempt : d))}
          rowIndex={rowIndex}
          reject={!!reject}
          />
        <Keyboard
          keyStates={letterUsage(keyword, attempts)}
          layout="abc"
          onKey={this.onKey}
          />
        <Modal open={modal === 'stats'} onClose={this.onModalClose}>
          <Stats
            gameOver={gameStatus !== ''}
            data={stats}
            rowIndex={rowIndex}
            // FIXME:
            shareText={`Orðla 210 4/6

⬜🟩⬜⬜⬜
⬜🟩🟩⬜⬜
⬜🟩🟩⬜⬜
🟩🟩🟩🟩🟩`}
            />
        </Modal>
        <Toaster ref={d => (this.toaster = d)} />
      </div>
    );
  }
}
